@import 'variables';

.medications-form {
    padding: 0 1rem;
    border-spacing: 0.75rem;

    th {
        font-size: 1.25rem;
        color: $pastel-blue;
    }

    tbody {
        tr {
            margin: 1rem 0; 
        }

        td {
            text-align: center;

            input {
                max-width: 20vw;
            }
        }
    }

    .delete-bttn {
        all: unset;
        background-color: #ddd;
        cursor: pointer;
        padding: 0 1rem;

        &:hover {
            background-color: aquamarine;
        }
    }

    button {
        align-self: start;
        &:hover {
            background-color: #ddd;
        }
    }
}